@import url(https://fonts.googleapis.com/css?family=Roboto:400,500&subset=latin-ext);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Roboto', sans-serif;
}
.App {
  display: grid;
  grid-template-rows: 10% 15% 34% 20% 9px 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas:
    "burger burger burger"
    "header header header"
    "image image image"
    "temp temp temp"
    "line line line"
    "sun wind pressure";
  height: 100vh;
}

.App.day {
  background-color: #FBFBFB;
}

.App.night {
  background-color: #040404;
}

@media(orientation: landscape) {
  .App {
    display: grid;
    grid-template-rows: 25% 34% 20% 1fr;
    grid-template-areas:
      "header header header"
      "image image image"
      "temp temp temp"
      "sun wind pressure";
  }

  body {
    background-color: #040404;
  }
}
/*______________________________________GENERAL BULID________________________*/
header.header {
    grid-area: header;
    text-align: center;
}

header.header h1 {
    font-size: 2.5rem;
    margin: 0;
    line-height: 10vh;
    text-transform: uppercase;
    font-weight: medium;
    letter-spacing: 2px;
}

header.header h4 {
    margin: 0;
    font-size: 1.3rem;
    letter-spacing: 2px;
}

/* _________________________________________________________________________ */

/*______________________________________DAY BULID________________________*/
.header.day h1 {
    color: #040404;
}

.header.day h4 {
    color: #5D5D5D;
}

/* _________________________________________________________________________ */


/*______________________________________NIGHT BULID________________________*/

.header.night h1 {
    color: #f5f6fa;
}

.header.night h4 {
    color: #7f8c8d;
}

/* _________________________________________________________________________ */

/* ________________________RESPONSIVE_________________________ */
@media(orientation: landscape) {
    .header.night {
        margin: 3% 0 0 5%;
    }
}
div.sunrise {
    grid-area: sun;
    text-align: center;
    margin-top: 20px;
}

.sunriseIco {
    color: #f79809;
}

div.wind {
    grid-area: wind;
    text-align: center;
    margin-top: 20px;
}

.windIco {
    color: #0ddce0;
}

div.pressure {
    grid-area: pressure;
    text-align: center;
    margin-top: 20px;
}

.pressureIco {
    color: #ed1e5c
}

div>h2 {
    font-weight: normal;
    line-height: 10vh;
    text-align: center;
}

div>h4 {
    color: #5D5D5D;
}

div.sunsetInfo.night {
    grid-area: pressure;
    text-align: center;
    margin-top: 20px;
}

.moonIco {
    color: #f7d709;
    -webkit-transform: rotate(-15deg);
            transform: rotate(-15deg);
}

div.night h2 {
    color: #f5f6fa;
    background-color: #040404;
}

div.night h4 {
    color: #7f8c8d;
}

/* ________________________RESPONSIVE_________________________ */
@media(orientation: landscape) {

    div.sunrise,
    div.wind,
    div.pressure {
        margin-top: -10px;
        overflow: hidden;
    }

    div.sunrise.night,
    div.wind.night,
    div.sunsetInfo.night {
        margin-top: -10px;
    }

}
/* ___________________________GENERAL BULID____________________________ */

div.image {
    grid-area: image;
    position: relative;
}

div.firstCloud {
    position: absolute;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 2;
}

div.secondCloud {
    position: absolute;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

/* ______________________________________________________________________ */

/* ___________________________DAY BULID____________________________ */

.day .cloud {
    color: #00a8ff;
    -webkit-animation: clouds 5s linear infinite alternate;
            animation: clouds 5s linear infinite alternate;
}

.day .firstCloud {
    top: 70%;
    left: 75%;
}

.day .secondCloud {
    top: 15%;
    left: 20%;
}

.image.day>.sun {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-image: -webkit-radial-gradient( circle, #f4b642, #f4c23a, #f2cf33, #eedc2d, #e7ea2a);
    background-image: radial-gradient( circle, #f4b642, #f4c23a, #f2cf33, #eedc2d, #e7ea2a);
    position: absolute;
    top: 40%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    box-shadow: 14px 15px 78px -25px rgba(0, 0, 0, 0.75);
}

/* ______________________________________________________________________ */

/* ___________________________NIGHT BULID____________________________ */

div.image.night {
    background: url(data:image/png;base64,);
}

.night>.secondCloud {
    top: 30%;
    left: 75%;
    font-size: 0.9rem;
}

.night>.firstCloud {
    top: 75%;
    left: 21%;
}

.image.night .cloud {
    color: #ecf0f1;
    -webkit-animation: clouds 5s linear infinite alternate;
            animation: clouds 5s linear infinite alternate;
}

.image.night .moon {
    color: #f7d709;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%) rotate(-15deg);
            transform: translate(-50%, -50%) rotate(-15deg);
    -webkit-animation: moon 3s linear infinite alternate;
            animation: moon 3s linear infinite alternate;
}

/* ______________________________________________________________________ */

/* ________________________RESPONSIVE_________________________ */

@media (orientation: landscape) {
    .image.day>.sun {
        width: 80px;
        height: 80px;
        top: 50%;
    }
    .day>.firstCloud {
        top: 40%;
        left: 70%;
    }
    .day>.secondCloud {
        top: 60%;
        left: 30%;
    }
    div.image.night {
        background: none;
    }
    .image.night .moon {
        font-size: 5rem;
    }
    .night>.secondCloud {
        top: 40%;
        left: 70%;
        font-size: 0.9rem;
    }
    .night>.firstCloud {
        top: 60%;
        left: 30%;
    }
}

@-webkit-keyframes clouds {
    from {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    to {
        -webkit-transform: scale(1.4);
                transform: scale(1.4);
    }
}

@keyframes clouds {
    from {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    to {
        -webkit-transform: scale(1.4);
                transform: scale(1.4);
    }
}

@-webkit-keyframes moon {
    from {
        -webkit-transform: translate(-50%, -50%) rotate(-15deg);
                transform: translate(-50%, -50%) rotate(-15deg);
    }
    to {
        -webkit-transform: translate(-50%, -50%) rotate(-25deg);
                transform: translate(-50%, -50%) rotate(-25deg);
    }
}

@keyframes moon {
    from {
        -webkit-transform: translate(-50%, -50%) rotate(-15deg);
                transform: translate(-50%, -50%) rotate(-15deg);
    }
    to {
        -webkit-transform: translate(-50%, -50%) rotate(-25deg);
                transform: translate(-50%, -50%) rotate(-25deg);
    }
}
/* _________________________GENERAL BULID________________________________ */
div.temp {
    grid-area: temp;
    text-align: center;
    font-size: 2rem;
}

div.temp h2 {
    font-size: 1.6rem;
    margin: 0 0 10px 0;
}

div.temp h2::after {
    content: '';
    height: 1.5px;
    display: block;
    grid-area: line;
    background-color: #95a5a6;
    width: 60%;
    margin: 0 auto;
}

.temp h2>span {
    color: #41a9f4;
    font-weight: bold;
}

/* ______________________________________________________________________ */


/* _________________________DAY BULID________________________________ */
.temp.day {
    color: #040404;
}

.temp.day h2 {
    color: #95a5a6;
}

/* ______________________________________________________________________ */

/* _________________________NIGHT BULID________________________________ */
.temp.night {
    color: #f5f6fa;

}

.temp.night h2 {
    color: #7f8c8d;
}

/* ______________________________________________________________________ */

/* ____________________________RESPONSIVE_________________________________ */
@media(orientation: landscape) {
    .temp h2 {
        display: none;
    }

    .temp h1 {
        font-size: 3rem;
    }


}
/* _______________________________GENERAL BULID___________________________ */
nav.burger {
    grid-area: burger;
    margin: 3% 0 0 5%;
    font-size: 1.2rem;
    z-index: 7;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
}

nav.menu {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    z-index: 5;
    background-color: #040404;
    width: 100vw;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
}

nav.menu.active {
    top: -100vh;
}

nav.menu input {
    display: block;
    border: none;
    border-bottom: 3px solid;
    -webkit-border-image: -webkit-linear-gradient(45deg, #743ad5, #d53a9d);
            border-image: linear-gradient(45deg, #743ad5, #d53a9d);
    border-image-slice: 1;
    background-color: inherit;
    position: absolute;
    top: 45%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 80%;
    text-align: center;
    font-size: 2rem;
    padding: 15px;
    color: #FBFBFB;
    outline: none;
}

.cancel {
    color: #FBFBFB;
    margin: 3% 0 0 5%;
}

.applay {
    z-index: 10;
    position: absolute;
    top: 65%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: inherit;
    border: solid 4px transparent;
    -webkit-border-image: -webkit-linear-gradient(45deg, #743ad5, #d53a9d);
            border-image: linear-gradient(45deg, #743ad5, #d53a9d);
    border-image-slice: 1;
    background: -webkit-linear-gradient(45deg, #743ad5, #d53a9d);
    background: linear-gradient(45deg, #743ad5, #d53a9d);
    padding: 10px 40px;
    color: #FBFBFB;
    font-size: 1.7rem;
    letter-spacing: 2px;
    text-transform: uppercase;
    outline: none;
}

.warning {
    color: #e74c3c;
    position: absolute;
    top: 80%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-align: center;
}

/* ______________________________________________________________________ */

/* _______________________________DAY BULID___________________________ */

nav.burger.day {
    color: #040404;
}

/* _______________________________________________________________________ */

/* _______________________________NIGHT BULID___________________________ */

nav.burger.night {
    color: #f5f6fa;
}

/* _______________________________________________________________________ */

.shake {
    -webkit-animation: shake .4s linear;
            animation: shake .4s linear;
}

/* ________________________RESPONSIVE_________________________ */
@media(orientation: landscape) {
    nav.burger {
        margin-top: 10px;
    }

    .burger.night {
        /* margin-top: 35px; */
        grid-area: header;
    }
}

@-webkit-keyframes shake {

    from,
    to {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate(-50%, -50%) translate3d(0, 0, 0);
    }

    10%,
    30%,
    50%,
    70%,
    90% {
        -webkit-transform: translate3d(-10px, 0, 0);
        transform: translate(-50%, -50%) translate3d(-10px, 0, 0);
    }

    20%,
    40%,
    60%,
    80% {
        -webkit-transform: translate3d(10px, 0, 0);
        transform: translate(-50%, -50%) translate3d(10px, 0, 0);
    }
}

@keyframes shake {

    from,
    to {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate(-50%, -50%) translate3d(0, 0, 0);
    }

    10%,
    30%,
    50%,
    70%,
    90% {
        -webkit-transform: translate3d(-10px, 0, 0);
        transform: translate(-50%, -50%) translate3d(-10px, 0, 0);
    }

    20%,
    40%,
    60%,
    80% {
        -webkit-transform: translate3d(10px, 0, 0);
        transform: translate(-50%, -50%) translate3d(10px, 0, 0);
    }
}
